* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.chat {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    padding: 2rem;
}

.chat .messages {
    overflow: auto;
    flex-grow: 1;
    margin-bottom: 1rem;
}

.chat .messages > .message:not(:last-child) {
    margin-bottom: 1rem;
}

.message {
    border: 1px solid black;
    border-radius: .5rem;
    padding: 1rem;
    margin-right: 25%;
}

.message.owner {
    margin-right: 0;
    margin-left: 25%;
}

.chat form {
    width: 100%;
    position: relative;
}

.chat form input {
    width: 100%;
    padding: 1rem;
}

.chat form .btn-send{
    position: absolute;
    top: calc(50% - 1rem);
    right: .5rem;
}

